<template>
  <div class="main">
    <cube-form :model="model" @submit="submitHandler">
      <cube-form-group>
        <!---->
        <cube-form-item :field="fields[0]"></cube-form-item>
        <!---->
        <cube-form-item :field="fields[1]"></cube-form-item>
      </cube-form-group>

      <cube-form-group>
        <cube-button type="submit"></cube-button>
      </cube-form-group>
    </cube-form>
    <router-link to="/register" class="reg"></router-link>
  </div>
</template>

<script>
import { loginApi } from "@/api/getData.js";
export default {
  data() {
    return {
      model: {
        phoneValue: "",
        pwdValue: "",
        nameValue: ""
      },
      fields: [
        {
          type: "input",
          modelKey: "phoneValue",
          label: "",
          props: {
            placeholder: ""
          },
          rules: {
            required: true
          },
          messages: {
            required: ""
          }
        },
        {
          type: "input",
          modelKey: "pwdValue",
          label: "",
          props: {
            placeholder: "",
            type: "password",
            eye: {
              open: false
            }
          },
          rules: {
            required: true
          },
          messages: {
            required: ""
          }
        }
      ]
    };
  },
  methods: {
    submitHandler(e, model) {
      e.preventDefault();
      loginApi(model.phoneValue, model.pwdValue).then(res => {
        if (res.data.code === 0) {
          //
          localStorage.setItem("token", res.data.data);

          this.$store.dispatch("setToken", res.data.data);

          const toast = this.$createToast({
            txt: "",
            type: "correct",
            time: 500,
            onTimeout: () => {
              //, 
              this.$router.push({ path: "/personal" });
            }
          });
          toast.show();
        } else {
          const toast = this.$createToast({
            txt: "",
            type: "error",
            time: 1500
          });
          toast.show();
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.main {
  padding: 50px 5% 0;
  text-align: center;
}
// 
.cube-btn {
  margin-top: 20px;
}
//
.reg {
  display: inline-block;
  margin-top: 30px;
  font-size: 18px;
}
</style>